
/* flickity */
.carousel-cell img {
    max-width: 100vw; }

.flickity-button {
    background: transparent !important; }

/* icon color */

.black-banner {
    position: relative;
    bottom: 0;
    height: 12vh;
    background-color: black;
    z-index: 4;
    margin-top: -3vh; }
/* eng-link */
.eng-link {
    position: absolute;
    top: 2vh;
    right: 1.5vw;
    z-index: 2;
    color: white; }
/* mais info section */
.hidden {
    display: none !important; }
.mais-info {
    position: fixed;
    z-index: 15;
    width: 93vw;
    margin-top: 4vh;
    display: flex;
    justify-content: center; }

.info-box {
    background-color: white;
    border: 2px solid black;
    width: 30vw;
    height: 85vh;
    overflow-y: scroll;
    position: relative; }

.info-close {
    position: sticky;
    top: 0vw;
    float: right;
    font-size: 1.5vw;
    padding: 1vw; }
.info-text {
    padding: 3vw 4vw 2vw 2vw; }

.more-info:hover {
    text-decoration: underline; }

/* sobre section */

#sobre {
    position: relative;
    background-color: black;
    color: white;
    text-align: center;
    padding: 10vh 0;
    z-index: 4;
    margin-top: -3vh;
    width: 100vw; }

.sobre-texto {
    padding: 0 5vw 0 15vw; }

.more-info {
    color: #FF4A6E; }

/* percurso section */

#percurso {
    background-color: #FF4A6E;
    padding: 10vh 0;
    color: white;
    text-align: center; }

.percurso-texto {
    padding: 0 12vw 0 3vw;
    margin-bottom: 10vh; }

.percurso-texto a, .pessoas-texto a, .sobre-texto a {
    color: white; }

.percurso-texto a:hover {
    color: white; }

.imagem-percurso {
    border: 2px solid black;
    width: 100%; }

/* pessoas section */
#pessoas {
    background-color: black;
    color: white;
    text-align: center;
    padding: 10vh 0; }

.pessoas-texto {
    padding: 0 5vw 0 18.75vw; }


.terry {
        width: 60vw;
        padding-left: 5vw; }
.marsha {
    width: 30vw;
    margin-left: 20vw; }

.pussyriot {
    width: 30vw;
    margin-top: 20vh;
    margin-left: 10vw; }

.elizabeth {
    width: 30vw;
    margin-left: 10vw;
    margin-top: -5vh; }

/* intervenções section */
#intervenções {
    background-color: #FF4A6E;
    padding: 10vh 0;
    color: white;
    text-align: center; }

.percurso-img {
    border: 2px solid black; }

.percurso1 {
    width: 37vw;
    margin-right: 2vw; }

.paisagem {
    width: 23vw;
    margin-left: 22vw;
    margin-top: -7vh; }
.cultura {
    width: 23vw;
    margin-left: 22vw;
    margin-top: 10vh; }

.bleeding {
    width: 23vw;
    margin-right: 58vw; }

/* contacts section */

#contactos {
    background-color: black;
    color: white;
    margin: 10vh 6vw; }

#contactos p {
    font-size: 0.7em;
    line-height: 1.4; }

#contactos h6 {
    font-size: 0.9em;
    line-height: 1.2; }

#contactos a {
    color: white;
    text-decoration: underline !important; }


/* footer */

.footer {
    background-color: black;
    border-top: 2px solid #FF4A6E;
    border-bottom: 2px solid #FF4A6E;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100vw;
    color: white;
    display: flex;
    z-index: 9;
    justify-content: space-between;
    align-items: center;
    padding: 1vw; }

.border-white {
    border-top: 1px solid white;
    width: 100vw;
    margin: 2vh 0; }


@media screen and(min-width: 1024px) {
    .sobre-video {
        width: 75%; }
    .percurso-width {
        width: 75%; }

    .sign-link {
        width: 50%;
        text-align: center; }

    .hashtag, .socials {
        width: 25%; }

    .hashtag {
        text-align: right; }

    .footer {
        padding: 1vw 2.5em; }

    .mobile-socials {
        display: none; } }


@media screen and(max-width: 1024px) {
    .sobre-texto {
        margin-top: 5vh !important;
        padding: 0; }

    .percurso-texto {
        padding: 0;
        margin-bottom: 5vh !important; }

    .pessoas-texto {
        padding: 0;
        margin: 5vh 0 !important; }

    .terry {
        width: 100%;
        padding: 0; }

    .marsha, .pussyriot, .elizabeth {
        width: 100%;
        padding: 2vh 3rem;
        margin: 0; }

    .percurso1, .paisagem, .cultura, .bleeding {
        width: 100% !important;
        margin: 0;
        padding: 0; }

    .percurso-padding {
        padding: 1vh 3rem; }

    .percurso-width {
        width: 100% !important;
        margin-bottom: 1vh; }

    .info-box {
        width: 80vw; }

    .mais-info {
        width: 100vw; }

    .info-close {
        font-size: 1.5em; }

    .info-text {
        padding: 4vw 7vw 3vw 4vw; }

    .socials, .sign-link {
        padding-top: 1vh; }

    .socials, .hashtag {
        display: none; }

    .mobile-socials {
        padding: 1vh 0; }

    .footer {
        flex-direction: column;
        align-items: center; }

    .menu li {
        margin-bottom: 3vh;
        font-size: 3vw; }

    .menu {
        width: 50vw; }

    .mini {
        font-size: 3vw; }

    .bottom-menu {
        position: static; } }

@media screen and(max-width: 835px) {
    .menu {
        width: 65vw; }
    .menu li {
        margin-bottom: 3.5vh;
        font-size: 4.5vw; }

    .mini {
        font-size: 2.4vw; } }
